<!-- 推荐歌曲页面 -->
<template>
  <div class="top-100-page">
    <div class="top-100-backtop" @click="backTop" v-show="isShowBackTop">
        <img src="@/assets/backtop.svg" width="30px">
    </div>
    <div class="top-100-item-box" ref="top100ItemBoxDom">
      <div v-if="newAlbums.length != 0" class="top-100-item" v-for="(item, index) in newAlbums" :key="index">
		
        <img class="top-100-album-img" @click="playThisAlbum(item.id)" :src="item.blurPicUrl" width="200px"/>
		<div class="top-100-album-info">
          <div class="top-100-song-name">{{ item.name }}</div>
          <div class="top-100-singer-name">{{ item.artist.name }}</div>
        </div>
		
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: "top-100-page",
  data() {
    return {
      scrollerPosition: 0,
      isShowBackTop: false,
      playingSongStyle: {
        "transform": "scale(1.1)",
      },
    };
  },
  computed: {
    ...mapState({
      newAlbums: state => state.newAlbums,
    })
  },
  mounted() {
    this.$store.commit('handleHotAlbumList')

    // 当滑块滑动到一定距离再出现
    this.$refs.top100ItemBoxDom.addEventListener('scroll', () => {
      if(this.$refs.top100ItemBoxDom.scrollTop > 600) {
        this.isShowBackTop = true
      } else {
        this.isShowBackTop = false
      }
    })
  },
  beforeRouteLeave(to, from, next) {
    //记下离开时滑块位置
    this.scrollerPosition = this.$refs.top100ItemBoxDom.scrollTop
    next()
  },
  activated() {
    //激活后恢复滑块位置
    this.$refs.top100ItemBoxDom.scrollTop = this.scrollerPosition
  },
  methods: {
	// 跳转专辑列表
    playThisAlbum(albumid) {
	  this.$router.push("AlbumListPage");
	  this.$store.commit("getAlbumMusic", albumid);
    },
	// 置顶图标
    backTop() {
      this.$refs.top100ItemBoxDom.scrollTop = 0
    },
  },
};
</script>

<style>
.top-100-page {
  width: 100%;
  height: 100%;
}

.top-100-title {
  width: 100%;
  height: 40px;
  line-height: 40px;
  text-align: center;
  font-size: 25px;
  font-weight: bolder;
}

.top-100-item-box {
  width: 100%;
  height: 100%;
  overflow: hidden scroll;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.top-100-backtop {
  width: 30px;
  height: 30px;
  margin-left: 755px;
  margin-top: 580px;
  position: fixed;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.1);
  z-index: 3;
}

.top-100-backtop:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.top-100-backtop:active {
  transform: scale(1.2);
}

.top-100-item {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  position: relative;
}

.top-100-item:hover {
  transform: scale(1.1);
  z-index: 1;
}

.top-100-album-img {
  border-radius: 20px;
  box-shadow: 2px 2px 10px 3px grey;
}

.top-100-album-img:hover {
  cursor: pointer;
}

.top-100-mark-img {
  width: 20px;
  height: 20px;
  position: absolute;
  bottom: 45px;
  left: 5px;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.1);
  z-index: 2;
}

.top-100-mark-img:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.top-100-mark-img:active {
  transform: scale(1.5);
}

.top-100-album-info {
  width: 100%;
}

.top-100-song-name {
  width: 200px;
  margin-top: 5px;
  color: white;
  font-weight: bold;
  font-size: 15px;
  text-align: center;
  display: block;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.top-100-singer-name {
  width: 200px;
  margin-top: 5px;
  color: white;
  font-size: 12px;
  text-align: center;
  display: block;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
</style>